@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;1,100&display=swap);

#header ._imgFixed {
    height: 22rem;
}

#header {
    width: 100vw;
    height: 80vh;
    display: flex;
    align-items: center;
}

#header ._mainHeading {
    text-transform: capitalize;
    margin-left: 0.8rem;
    font-size: 2.3rem;
    font-weight: 700;
}

#header ._strongText {
    color: var(--mainColor);
}

#header ._secondaryHeading {
    margin-left: 0.8rem;
    font-size: 1.4rem;
}

#header ._btnGetStarted {
    margin-left: 0.8rem;
    font-size: 1.5rem;
    border: 2px solid var(--mainColor);
    padding: 0.4rem 1rem;
    text-decoration: none;
    border-radius: 0.5rem;
    color: var(--mainColor);
    text-transform: capitalize;
    font-weight: 500;
    transition: 0.5s;
}

#header ._btnGetStarted:hover {
    background: var(--mainColor);
    color: #fff;
}

#header ._webDeviceAnimated {
    animation: upDown 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes upDown {
    0% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100% {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}

@keyframes upDown {
    0% {
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100% {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

:root {
    --mainColor: #55efc4;
}

.navbar {
    background: #fff;
}

.nav-item {
    padding: 0rem 1rem !important;
}

.navbar a {
    text-transform: capitalize;
    font-size: 1.2rem;
}

.navbar a:hover {
   color: #55efc4;
   color: var(--mainColor);
}

.navbar-brand {
    font-size: 1.5rem !important;
    color: #55efc4 !important;
    color: var(--mainColor) !important;
}

._listActive {
    font-weight: bold;
    border-bottom: 1px solid #55efc4;
    border-bottom: 1px solid var(--mainColor);
}

/* Responsive ---> */
@media (max-width: 991px) {
    
    ._navbar-brand {
        font-size: 1rem !important;
    }

    #header ._mainHeading { font-size: 1.6rem; }   
    #header ._secondaryHeading { font-size: 1.1rem; } 
    #header ._btnGetStarted { font-size: 1rem; }
    footer ._footerSection { font-size: 0.7rem}

    ._listActive {
        border-bottom: none;
    }

    #header {
        height: 80vh;
        text-align: center;
    }

    #header ._imgFixed {
        text-align: center;
        height: 10rem;
    }
}

@media only screen and (max-width: 767px) {

    #header._imgFixed { height: 15rem; }
    #header ._mainHeading { font-size: 1.6rem; }   
    #header ._secondaryHeading { font-size: 1.1rem; } 
    #header ._btnGetStarted { font-size: 1rem; }
    footer ._footerSection { font-size: 0.7rem}
}
